body {
  background-color: #3b4d5b;

}

.App {
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.player {
  color: #fff;
  margin-bottom: 40px;
  /* padding: 30px; */
}
.header{
  color: white;
  text-shadow: 1px 3px 5px black  1;
  text-align: center;
  margin: 10px;
}
.reset {
  margin-top: 20px;
  padding: 5px 10px;
  border-radius: 10px;
  border: none;
  font-size: 15px;
  font-weight: bold;
  cursor: pointer;
}

.reset:hover {
  background-color: rgba(255, 255, 255, 0.675);
}

.square {
  background: transparent;
  color: white;
  border: 1px solid #fff;
  float: left;
  font-size: 40px;
  font-weight: bold;
  line-height: 34px;
  height: 70px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 70px;
}

.board-row {
  display: table;
}